@import "https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700;800&display=swap";
:root {
  --nav-hover: #0d598f;
  --home-font: white;
  --nav-sticky: #081522;
  --card-background: #082942;
}

* {
  font-family: Montserrat, sans-serif;
}

a {
  text-decoration: none;
}

.navbar * {
  font-weight: 600;
  color: var(--home-font) !important;
}

.navbar.sticky {
  opacity: .8;
  background-color: var(--nav-sticky) !important;
}

.navbar .navbar-brand img {
  width: 100px;
  display: none;
}

.navbar.sticky .navbar-brand img {
  width: 100px;
  display: inline;
}

.navbar-brand:hover, .nav-item a:hover {
  color: var(--nav-hover) !important;
}

.main-image img {
  width: 100%;
  height: 100vh;
}

#about {
  transform: translateZ(1px);
  padding-top: 65px !important;
}

.about__name {
  letter-spacing: 10px;
  color: #fff;
  font-weight: 700;
}

.about__short {
  letter-spacing: 1px;
  color: #fff;
  padding-bottom: 10px;
  font-weight: 500;
}

.description {
  color: #fff;
  letter-spacing: 1px;
  text-align: justify;
  background-color: var(--card-background);
  border-radius: 5px;
  padding: 10px;
  line-height: 25px;
}

.description__image {
  height: 500px;
  object-fit: contain;
  object-position: top;
}

.icons__logos i {
  cursor: pointer;
  color: #fff;
  margin-right: 15px;
  font-size: 30px;
  transition: all .4s;
}

.icons__logos i:hover {
  transform: scale(.8);
}

.btn-cv {
  width: -moz-fit-content;
  width: fit-content;
  background-color: #0f5285;
  border: none;
  padding: 10px 20px;
}

.btn-cv:hover {
  background-color: #0d598f;
}

.btn-cv i {
  font-size: 20px;
}

.topic {
  color: #fff;
  margin-top: 40px;
  margin-bottom: 20px;
}

.topic h2 {
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 1px;
  grid-gap: 22px;
  grid-template-rows: 16px 0;
  grid-template-columns: 1fr auto 1fr;
  font-size: 26px;
  font-weight: 800;
  display: grid;
}

.topic h2:after, .topic h2:before {
  content: " ";
  border-bottom: 2px solid #ccc;
  display: block;
}

.portfolio-works__topic {
  padding-top: 60px;
}

.portfolio-works__container {
  flex-wrap: wrap;
  justify-content: center;
  column-gap: 10px;
  display: flex;
}

.box {
  padding: 0 25px;
}

.card {
  width: 250px;
  height: 250px;
  border-radius: 10px;
  margin-bottom: 20px;
  transition: all .4s;
}

.card:hover {
  cursor: pointer;
  transform: scale(.9);
}

.card-img {
  border-radius: 10px;
}

.google-img {
  width: 250px;
  height: 250px;
  object-fit: cover;
}

.twitter-card {
  background-color: #00aced;
}

.twitter-img {
  width: 240px;
  height: 250px;
  object-fit: contain;
}

.pos-img {
  width: 150px;
  height: 150px;
  margin: 50px 0 0 50px;
}

.pro-edit-card {
  background-color: #7671de;
}

.pro-edit-img {
  object-fit: contain;
  margin-top: 30px;
}

.al-table * {
  font-size: 15px;
}

.list-group-item {
  color: #fff;
  background-color: #0000;
}

#contact {
  background-color: #0000;
}

.contact__icons {
  z-index: 99;
}

.footer-img {
  width: 80px;
}

#linkedin {
  color: #0e76a8;
}

#mail {
  color: gray;
}

#twitter {
  color: #00acee;
}

@media (max-width: 641px) {
  footer {
    justify-content: center !important;
  }
}

